@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
.headerSection{display:flex;justify-content:flex-end;width:100%}.headerSection a{display:flex;align-items:center;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:0.2% 1%;background-color:rgba(172,212,250,0.75);-webkit-backdrop-filter:blur(3px);backdrop-filter:blur(3px);position:fixed;z-index:999;border-bottom-left-radius:30px;text-decoration:none;color:#0c0c0c}

.introSection{display:flex;margin:0 0 5% 0}.introSection .intro{display:flex;flex-direction:column;justify-content:space-between;background-color:#5ebf8d;height:auto;border-radius:30px}.introSection .intro .introDescription{display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;height:85%;padding:0 2%}.introSection .intro .introDescription p{text-align:center}.introSection .intro .callToActionContainer{height:15%;display:flex;flex-direction:row-reverse;align-items:flex-end}.introSection .intro .callToActionContainer a{text-decoration:none;z-index:99}.introSection .intro .callToActionContainer a #button{float:right;border-radius:30px 0 30px 0;background-color:#646ae0;background-image:linear-gradient(to top, rgba(0,0,0,0.2), #646ae0, #646ae0, rgba(255,255,255,0.4));color:#0c0c0c;white-space:nowrap;padding:2% 10%;border:1px solid #646ae0;text-shadow:0 1px 1px rgba(0,0,0,0.3);box-shadow:rgba(0,0,0,0.3) -1px -1px 3px;transition:0.3s ease-in-out}.introSection .intro .callToActionContainer a #button:hover{transform:scale(1.1);transform-origin:right bottom;transition:0.3s ease-in-out;border-radius:26px 0 26px 0}.introSection #introImg img{width:42vw;opacity:0.75}

.aboutMe{margin:5% 0}.aboutMe .AboutMeInner{display:flex;justify-content:space-evenly;background-color:#5ebf8d;border-radius:30px;padding:2%}.aboutMe .AboutMeInner .selfDescription{display:flex;width:35%;flex-direction:column;justify-content:space-evenly}.aboutMe .AboutMeInner .selfDescription p{justify-self:center;text-align:center}.aboutMe .AboutMeInner #image{height:70vh}.aboutMe .AboutMeInner #image img{height:100%;border-radius:30px;box-shadow:4px 8px #fdfcfa}

.project{display:flex;flex-direction:column;width:25%;margin:2%}.project .project-tile{display:flex;flex-direction:column;border-radius:5px;text-decoration:none}.project .project-tile:hover{box-shadow:#646ae0 0px -3px 4px,#646ae0 3px 0px 4px,#646ae0 -3px 0px 4px;transition:1s ease-in-out}.project .project-img{width:100%;border-radius:5px 5px 0 0}.project .project-title{display:flex;background-color:#646ae0;background-image:linear-gradient(to top, #646ae0, rgba(255,255,255,0.4));color:#0c0c0c;text-align:center;justify-content:center;align-items:center;height:3rem;padding:3% 3%;border-radius:0 0 5px 5px}.project .code{opacity:0;transition:0.4s ease-in-out}.project .project-title,.project .code{font-size:1.4rem}.project .project-tile:hover .code{color:#0c0c0c;opacity:1}@media (max-width: 1000px){.project .project-title,.project .code{font-size:1.2rem}}@media (max-width: 600px){.project{width:80%}.project .project-title,.project .code{font-size:1.2rem;color:#000}}@media (min-width: 1400px){.project{width:15%}.project .project-title,.project .code{font-size:1rem}}

.projects .projectsContainer{display:flex;flex-direction:column;padding:5%;background-color:#5ebf8d;border-radius:30px}.projects .projectsContainer .project-tiles{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-evenly}@media (max-width: 1000px){.projects{width:100%;padding:0}}@media (max-width: 600px){.projects{margin:0 auto;padding:0}}

.contact{width:100%}.contact .socialIcons{display:flex;flex-direction:row;justify-content:center;background-color:#5ebf8d;border-radius:30px;padding:2%}.contact .socialIcons ul{position:relative;margin:0;padding:0;display:flex}.contact .socialIcons ul li{position:relative;list-style:none;width:4rem;height:4rem;margin:2rem 2rem}.contact .socialIcons ul li span{border-radius:10px;position:absolute;top:0;left:0;width:100%;height:100%;background:#000;transition:0.5s;display:flex !important;align-items:center;justify-content:center;color:#fff;font-size:3rem !important}.contact .socialIcons ul li:hover span{box-shadow:1px 1px 1px 1px rgba(0,0,0,0.2)}.contact .socialIcons ul li:hover span:nth-child(6){transform:scale(1.2);opacity:1}.contact .socialIcons ul li:hover span:nth-child(5){transition-delay:0.2s;transform:scale(1.2) rotate(45deg);background-color:#fff;border-radius:1px;opacity:1}.contact .socialIcons ul li:hover span:nth-child(4){transform:translate(20px, -20px);transition-delay:0.05s;border-radius:40px;opacity:0.6}.contact .socialIcons ul li:hover span:nth-child(3){transform:translate(-20px, 20px);border-radius:40px;transition-delay:0.05s;opacity:0.6}.contact .socialIcons ul li:hover span:nth-child(2){transform:translate(-20px, -20px);border-radius:40px;transition-delay:0.05s;opacity:0.6}.contact .socialIcons ul li:hover span:nth-child(1){transform:translate(20px, 20px);border-radius:40px;transition-delay:0.05s;opacity:0.6}.contact .socialIcons ul li:nth-child(1) span{background:#de5246}.contact .socialIcons ul li:nth-child(2) span{background:#0072b1}.contact .socialIcons ul li:nth-child(3) span{background:#00acee}.contact .socialIcons ul li:nth-child(4) span{background:#171515}.contact .socialIcons ul li:nth-child(5) span{background:#3f729b}

.skills .skillsList{padding:2%;display:flex;flex-direction:column;align-items:center;background-color:#5ebf8d;border-radius:30px;text-align:center}.skills .skillsList img{height:30px;margin:0 5px}.skills .skillsList h3{text-align:center;margin:0 auto;margin-bottom:1rem;width:60%;background:linear-gradient(to left, #5ebf8d, #0c0c0c, #5ebf8d 100%);background-position:0 100%;background-size:100% 2px;background-repeat:repeat-x}.skills .skillsList .skillsSubSection{padding:2%}

#homeContent{display:flex;flex-direction:column;margin:5% auto;padding:5%;width:80%}

.footer{background-color:#acd4fa;text-align:center;margin-bottom:0}.footer p{padding:1rem}.footer a{text-decoration:none}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}body{background-color:#f9f3ed;font-family:'Quicksand', sans-serif;font-size:16pt;font-weight:lighter;color:#0c0c0c;display:flex;align-items:center;scroll-behavior:smooth}h1{font-size:40pt;font-weight:bold}h2{font-size:28pt;font-weight:medium}h3{font-size:24pt}h4{font-size:20pt}h5{font-size:18pt}.section{margin:5% auto}.sectionTitle{display:flex;background-color:#fdfcfa;border-radius:30px 0 30px 0;border:1px solid #0c0c0c;padding:0.5% 1%;width:-webkit-max-content;width:-moz-max-content;width:max-content;position:absolute;box-shadow:#0c0c0c 1px 1px 3px}

