@import '../color.scss';

.project {
	display: flex;
	flex-direction: column;
	width: 25%;
	margin: 2%;

	.project-tile {
		display: flex;
		flex-direction: column;
		border-radius: 5px;
		text-decoration: none;
	}

	.project-tile:hover {
		box-shadow:
      /* top */ $actionItem 0px -3px 4px,
			/* right */ $actionItem 3px 0px 4px,
			/* left */ $actionItem -3px 0px 4px;
		transition: 1s ease-in-out;
	}

	.project-img {
		width: 100%;
		border-radius: 5px 5px 0 0;
	}

	.project-title {
		display: flex;
		background-color: $actionItem;
		background-image: linear-gradient(
			to top,
			$actionItem,
			rgba(255, 255, 255, 0.4)
		);
		color: $primaryText;
		text-align: center;
		justify-content: center;
		align-items: center;
		height: 3rem;
		padding: 3% 3%;
		border-radius: 0 0 5px 5px;
	}

	.code {
		opacity: 0;
		transition: 0.4s ease-in-out;
	}

	.project-title,
	.code {
		font-size: 1.4rem;
	}

	.project-tile:hover .code {
		color: $primaryText;
		opacity: 1;
	}

	//*Tablet Styling
	@media (max-width: 1000px) {
		.project-title,
		.code {
			font-size: 1.2rem;
		}
	}

	//*Phone Styling
	@media (max-width: 600px) {
		width: 80%;
		.project-title,
		.code {
			font-size: 1.2rem;
			color: rgba(0, 0, 0, 75%);
		}
	}

	//*Large Monitor Styling
	@media (min-width: 1400px) {
		width: 15%;

		.project-title,
		.code {
			font-size: 1rem;
		}
	}
}
